  .overlay {
    overflow: auto;
  }
}

.modal {
  opacity: 0;
  visibility: hidden;
  z-index: 30;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

